body{
    background-image: url("../images/backgroundCircle.png");
}

html {
    scroll-behavior: smooth;
  }

a {
    color: #2e6c70 !important;
  }

p {
    font-size: 1em;
    font-family: 'Poppins', sans-serif;
}

h1, h2{
    font-family: 'Cormorant', serif;
}

.about-strong{
    font-size: 1.1em;
}

.nav-link, .navbar-brand{
    margin-top: 30px;
    font-family: 'Poppins', sans-serif;
}

.blob-align{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.blob-one{
    height: 80%;
    background-color: #d4e2d4;
    border-radius: 24% 76% 27% 73% / 46% 74% 26% 54% ;
    margin-top: 15% ;
    margin-left: 5%;
    animation: firstMorph 15s ease-in-out infinite;
    transition: all 3s ease-in-out;
}


.blob-two{
    height: 400px;
    background-color: #d8d3cd;;
    border-radius: 44% 56% 56% 44% / 38% 68% 32% 62%;
    margin-top: 30%;
    animation: secondMorph 8s ease-in-out infinite;
    transition: all 2s ease-in-out;
}    

.blob-three{
    height: 90%;
    background-color:#dbe3e5;
    border-radius: 24% 76% 27% 73% / 46% 64% 36% 54% ;
    animation: thirdMorph 15s ease-in-out infinite;
    transition: all 3s ease-in-out;
}

.blob-four{
    height: 80%;
    background-color: #ffbb91;
    border-radius: 44% 56% 56% 44% / 38% 68% 32% 62%;
    margin-bottom: 30%;
    animation: secondMorph 8s ease-in-out infinite;
    transition: all 2s ease-in-out;
}

@keyframes firstMorph {
    0% {border-radius:  24% 76% 27% 73% / 46% 74% 26% 54%;} 
    50% {border-radius:  85% 15% 63% 37% / 43% 19% 81% 57%;}
    100% {border-radius:  24% 76% 27% 73% / 46% 74% 26% 54%;} 
  }

@keyframes secondMorph {
    0% {border-radius:  44% 56% 56% 44% / 38% 68% 32% 62%;} 
    50% {border-radius:  54% 46% 70% 30% / 56% 30% 70% 44%;}
    100% {border-radius:  44% 56% 56% 44% / 38% 68% 32% 62%;} 
  }
  
  @keyframes thirdMorph {
      0% {border-radius:  36% 64% 82% 18% / 73% 72% 28% 27%;} 
      50% {border-radius:  61% 39% 49% 51% / 49% 58% 42% 51%  ;}
      100% {border-radius:  36% 64% 82% 18% / 73% 72% 28% 27%;} 
    }

.intro{
    max-width: 60%;
    font-size: 1.25em;
}

.blob-project-one-back{
    background-color: #d3e0ea;
    animation: secondMorph 8s ease-in-out infinite;
    transition: all 4s ease-in-out;
}

.blob-project-two-front{
    background-color: #ece2e1;
    animation: firstMorph 22s ease-in-out infinite;
    transition: all 3s ease-in-out;
}

